.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}


.container, .container-fluid {
  margin-right: auto;
  margin-left: auto;
  padding-left: 2px;
  padding-right: 3px;
}

@media (min-width: 768px) {
  .container {
    width: 725px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 945px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}