body {
  position: relative;
  margin: 0;
  min-height: 100vh;
  padding-right: 0 !important;
  color: #fff;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
  background: #000;
}
.fancybox-margin {
  margin-right: 0 !important;
}
section {
  /*     overflow: hidden; */
  padding: 60px 0;
}
@media only screen and (min-width: 1200px) {
  section {
    padding: 80px 0;
  }
}
@media only screen and (min-width: 1600px) {
  section {
    padding: 110px 0;
  }
}
.white {
  background: #fff;
  color: #121221;
}
.link__accent {
  font-weight: 700;
  color: #009dd9;
}
.section__title {
  display: table;
  position: relative;
  margin: auto;
  font-size: 36px;
  font-weight: 700;
}
.section__title:before {
  content: '';
  position: absolute;
  height: 2px;
  width: 77px;
  top: 50%;
  transform: translateY(-50%);
  left: -110px;
  background: #b0b0b0;
}
.section__title:after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -110px;
  background-image: url(../images/icons/spritebox-sprite.png);
  background-repeat: no-repeat;
  background-position: -214px -39px;
  height: 7px;
  width: 81px;
}
.section__undertitle {
  padding: 60px 0;
  font-size: 21px;
  text-align: center;
}
::-webkit-input-placeholder {
  font-size: 16px;
}
::-moz-placeholder {
  font-size: 16px;
}
:-ms-input-placeholder {
  font-size: 16px;
}
:-moz-placeholder {
  font-size: 16px;
}
.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 10px 15px 0;
}
.header__logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo-img {
  margin-right: 15px;
}
.header__link {
  margin-right: 50px;
  position: relative;
}
.header__link:after {
  content: '';
  position: absolute;
  margin-bottom: -10px;
  bottom: 0;
  left: 0;
  width: 0%;
  border-bottom: 1px solid #fff;
  transition: 0.4s;
}
.header__link:hover:after {
  width: 100%;
}
.header__center {
  padding: 70px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__img {
  margin-left: -20px;
}
.header__title {
  font-size: 55px;
  font-weight: 300;
}
.header__text {
  padding-top: 20px;
  font-size: 30px;
  color: #ed901a;
}
