.system__box {
  padding: 40px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.system__box-img {
  flex-basis: 50%;
}
.system__box-img img {
  margin: auto;
}
.system__box-content {
  flex-basis: 48%;
}
.system__titles {
  padding-bottom: 25px;
  font-size: 28px;
  font-weight: 700;
}
.system__accent {
  display: block;
  font-size: 18px;
  margin-bottom: 25px;
}
.system__item {
  position: relative;
  padding-left: 20px;
  margin-bottom: 15px;
  max-width: 420px;
}
.system__item:before {
  content: '';
  position: absolute;
  top: 50%;
  left: -15px;
  transform: translateY(-50%);
  background-image: url(../images/icons/spritebox-sprite.png);
  background-repeat: no-repeat;
  background-position: -195px -39px;
  height: 13px;
  width: 19px;
}
.system .reorder .system__box-content {
  padding-left: 50px;
}
.system .reorder .system__box-img {
  order: 2;
}
.ask {
  padding: 40px 0 80px 190px;
  background: url(../images/back__small.jpg) no-repeat center / cover;
  color: #000;
}
.ask__box {
  position: relative;
}
.ask__title {
  font-size: 38px;
  font-weight: 700;
}
.ask__accent {
  display: inline-block;
  padding: 5px 0;
  font-size: 28px;
  font-weight: 700;
}
.ask__text {
  padding-top: 5px;
  font-size: 22px;
}
.ask__img {
  position: absolute;
  top: -250px;
  right: -110px;
}
.advantages__title:after {
  background-image: url(../images/icons/spritebox-sprite.png);
  background-repeat: no-repeat;
  background-position: -148px -55px;
  height: 7px;
  width: 98px;
}
.advantages__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 100px 0;
}
.advantages__item {
  position: relative;
  margin-bottom: 60px;
  font-size: 20px;
  font-weight: 300;
  counter-increment: li;
}
.advantages__item svg {
  display: block;
  position: absolute;
  top: -60px;
  width: 130px;
  height: 130px;
}
.advantages__item div {
  display: inline-block;
  margin-left: 15px;
}
.advantages__item:before {
  content: counter(li);
  position: absolute;
  top: 0;
  transform: translateY(-50%);
  color: #fff;
  font-size: 54px;
  z-index: 10;
}
.advantages__item:nth-of-type(1) {
  padding-left: 80px;
  max-width: 790px;
}
.advantages__item:nth-of-type(1):before {
  left: -19px;
}
.advantages__item:nth-of-type(1) svg {
  left: -70px;
}
.advantages__item:nth-of-type(2) {
  max-width: 790px;
}
.advantages__item:nth-of-type(2):before {
  top: -10px;
  right: -130px;
}
.advantages__item:nth-of-type(2) svg {
  top: -70px;
  right: -180px;
}
.advantages__item:nth-of-type(3) {
  padding-left: 48px;
  padding-top: 15px;
  width: 800px;
}
.advantages__item:nth-of-type(3):before {
  left: -80px;
}
.advantages__item:nth-of-type(3) svg {
  left: -130px;
}
.advantages__item:nth-of-type(4) {
  padding-left: 150px;
  max-width: 730px;
}
.advantages__item:nth-of-type(4):before {
  top: -9px;
  right: -75px;
}
.advantages__item:nth-of-type(4) svg {
  top: -70px;
  right: -130px;
}
.advantages__item:nth-of-type(5) {
  padding-left: 120px;
  width: 860px;
}
.advantages__item:nth-of-type(5):before {
  top: -5px;
  left: 6px;
}
.advantages__item:nth-of-type(5) svg {
  top: -70px;
  left: -45px;
}
.efficiency__box {
  padding: 50px 50px;
  display: flex;
  justify-content: space-between;
  text-align: center;
}
.efficiency__box-side {
  flex-basis: 45%;
}
.efficiency__titles {
  padding-bottom: 15px;
  font-size: 26px;
  font-weight: 700;
}
.efficiency__img {
  max-width: 436px;
  height: 255px;
}
.price__box {
  display: flex;
  justify-content: space-between;
}
.price__item {
  flex-basis: 30%;
  box-shadow: 0px 11px 16.74px 1.26px rgba(0, 0, 0, 0.27);
  background: #e1e1e1;
}
.price__item-active {
  margin-top: -10px;
  transform: scale(1.05);
  box-shadow: 0px 11px 16.74px 1.26px rgba(0, 0, 0, 0.27);
}
.price__titles {
  position: relative;
  padding: 15px 0;
  background: #ebebeb;
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  z-index: 10;
}
.price__titles:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  border: 30px solid transparent;
  border-top: 30px solid #fab915;
  border-right: 30px solid #fab915;
}
.price__sum {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
