@keyframes arrow-bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-6px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes smoothScroll {
  0% {
    opacity: 0;
    transform: translateY(-40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes antiSmoothScroll {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-40px);
  }
}
@keyframes pulsed {
  0% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0.4); */
    transform: scale(1);
  }
  70% {
    /* box-shadow: 0 0 0 10px rgba(204,169,44, 0); */
    transform: scale(1.05);
  }
  100% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0); */
    transform: scale(1);
  }
}
@keyframes growLine {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.slick-prev,
.slick-next {
  width: 50px;
  height: 100%;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
  transition: opacity .3s linear;
  z-index: 10;
}
.slick-prev {
  left: -25px;
  background-image: url(../images/icons/spritebox-sprite.png);
  background-repeat: no-repeat;
  background-position: -74px 0px;
  height: 62px;
  width: 74px;
}
.slick-next {
  right: -25px;
  background-image: url(../images/icons/spritebox-sprite.png);
  background-repeat: no-repeat;
  background-position: 0px 0px;
  height: 62px;
  width: 74px;
}
.slick-prev:before,
.slick-next:before {
  width: 0;
  height: 0;
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
  opacity: .35!important;
}
.slick-dots {
  display: inline-block !important;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
}
.slick-dots li {
  margin: 7px;
  width: 13px;
  height: 13px;
}
.slick-dots li button {
  background: #000;
  display: block;
  height: 5px;
  width: 5px;
  font-size: 0px;
  border-radius: 50%;
  border: 1px solid transparent;
  opacity: 0.75;
  transition: all .3s linear;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
  display: none;
}
.slick-dots li.slick-active button {
  opacity: 1;
  transform: scale(2);
  border: 1px solid #000;
  background: transparent;
}
.slick-dots li.slick-active button {
  background: transparent;
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
